
import { defineComponent } from 'vue';
import TodoItem from '@/components/TodoItem.vue';
export default defineComponent({
  props: ['todos'],
  components: {
    TodoItem,
  },
  methods: {
    switchTodo(id: string) {
      this.$emit('switchTodo', id);
    },
    removeTodo(id: string) {
      this.$emit('removeTodo', id);
    },
  },
});
