
import { defineComponent } from 'vue';

export default defineComponent({
  data() {
    return {
      title: '',
    };
  },
  methods: {
    onSubmit() {
      if (this.title.trim()) {
        const newTodo = {
          id: Date.now(),
          title: this.title,
          completed: false,
        };

        this.$emit('creatTodo', newTodo);
        this.title = '';
      }
    },
  },
});
